'use strict';

/**
 * --------------------------------
 * Require.js 
 * --------------------------------
 */
require.config({
	paths: {
		jquery     : 'libs/jquery.min',
		modernizr  : 'libs/modernizr',
		detectizr  : 'libs/detectizr.min',
		toggleGrid : 'guide/toggleGrid'
	},
	shim: {
		modernizr: {
			exports: 'Modernizr'
		},
		detectizr: {
			deps    : ['modernizr'],
			exports : 'Detectizr'
		},
	}
});


/**
 * --------------------------------
 *      
 * --------------------------------
 */
require(['detectizr']);

require(['toggleGrid'], function(toggleGrid) {
	// (Arguments)
	// arg1:     
	// arg2:     
	toggleGrid('body', 'grid');
});
define("main", function(){});

